import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import classes from './NavCard.module.css';
import Articles from './Articles/Articles';
import Projects from './Projects/Projects';
import Skills from './Skills/Skills';
import OpenSource from './OpenSource/OpenSource';
import ContentList from './ContentList/ContentList';

class NavCard extends React.Component {
  render() {
    return (
      <div className={classes.NavContent}>
        <div className={classes.Card}>
          <Switch>
            <Route path="/portfolio/articles" component={Articles} />
            <Route path="/portfolio/projects" render={Projects} />
            <Route path="/portfolio/opensource" component={OpenSource} />
            <Route path="/portfolio/skills" component={Skills} />
            <Route exact path="/portfolio" component={ContentList} />
            <Redirect to="/portfolio"/>
          </Switch>
        </div>
      </div>
    );
  }
}

export default NavCard;
